class NavDropDown {
  constructor(element, sidenav) {
    this.element = element;
    this.element.addEventListener("click", this.toggle);
    this.sidenav = sidenav;
  }

  toggle = (e) => {
    e.preventDefault();
    if (this.sidenav.classList.contains("active"))
      this.element.classList.toggle("active");
  };

  static init(sidenav) {
    Array.from(document.querySelectorAll(".has-dropdown")).forEach(
      (element) => {
        new this(element, sidenav);
      }
    );
  }
}

function initSideNav(toggler) {
  const sidenav = document.querySelector(".sidenav");
  toggler.addEventListener("click", (e) => {
    e.preventDefault();
    if (sidenav.classList.contains("active")) {
      // hide all .active .has-dropdown elements
      const activeDropdowns = sidenav.querySelectorAll(".has-dropdown.active");
      [...activeDropdowns].forEach((dropdown) =>
        dropdown.classList.remove("active")
      );
    }
    sidenav.classList.toggle("active");
  });
  return sidenav;
}

export default {
  init: function () {
    // initialize sidnave toggler
    const sidenav = initSideNav(document.querySelector(".nav-toggler"));

    // initialize dropdown
    NavDropDown.init(sidenav);
  },
};
